<template>
  <div class="detail-nav-bar" >
    <nav-bar>
      <img src="~assets/img/common/back.svg" slot="left" alt="" class="back" @click="backClick">
      <ul slot="center" class="title">
        <li v-for="(item, index) in titles" class="title-item" 
          @click="titleClick(index)" :class="{active: currentIndex === index}">{{ item }}</li>
      </ul>
    </nav-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";

export default {
  components: {
    NavBar
  },
  data () {
    return {
      titles: ['商品', '参数', '评论', '推荐'],
      currentIndex: 0
    }
  },
  methods: {
    titleClick(index) {
      this.currentIndex = index;
      this.$emit('titleClick', index);
    },
    backClick() {
      this.$router.back();
    }
  }
}
</script>

<style scoped>
  .back {
    vertical-align: middle;
  }

  .title {
    display: flex;
    font-size: 0.7rem;
  }

  .title-item {
    flex: 1;
  }

  .active {
    color: var(--color-tint);
  }
</style>
