<template>
  <div class="detail">
    <detail-nav-bar class="nav-bar" @titleClick="navBarClick" ref="navbar" />
    <scroll class="scroll-content" ref="scroll" :probeType="3" @scroll="scroll" >
      <detail-swiper :topImages="topImages" />
      <detail-base-info :goods="goods" />
      <detail-shop-info :shops="shops"/>
      <detail-goods-info :detailInfo="detailInfo" />
      <detail-param-info :paramInfo="paramInfo" ref="paramInfo" />
      <detail-comment-info :commentInfo="commentInfo" ref="commentInfo" />
      <detail-recommend-info :recommends="recommends" ref="recommendsInfo" />
    </scroll>
    <back-top @click.native="backTopClick" v-show="isShowBackTop" />
    <detail-bottom-bar @addToCart="addToCart" />
  </div>
</template>

<script>
// 公共组件
import Scroll from "components/common/scroll/Scroll";
import BackTop from "components/content/backtop/BackTop";
import { backTopMixin } from "common/mixin";

// 子组件
import DetailNavBar from "./childComps/DetailNavBar";
import DetailSwiper from "./childComps/DetailSwiper";
import DetailBaseInfo from "./childComps/DetailBaseInfo";
import DetailShopInfo from "./childComps/DetailShopInfo";
import DetailGoodsInfo from "./childComps/DetailGoodsInfo";
import DetailParamInfo from "./childComps/DetailParamInfo";
import DetailCommentInfo from "./childComps/DetailCommentInfo";
import DetailRecommendInfo from "./childComps/DetailRecommendInfo";
import DetailBottomBar from "./childComps/DetailBottomBar";

// 三方框架库
import { getDetail, Goods, Shops, GoodsParam, getRecommend } from "network/detail";
import {itemMixin} from "common/mixin";
import { mapActions } from "vuex";

// 工具
import { debounce } from "common/debounce";

import detaitil from "../../../../copy2/public/data/detail/detail.json";

export default {
  name: 'Detail',
  components: {
    Scroll,
    BackTop,
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    DetailRecommendInfo,
    DetailBottomBar
  },
  mixins: [backTopMixin],
  data () {
    return {
      iid: null,
      topImages: [],
      goods: {},
      shops: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      recommends: [],
      themeTopYs: [],
      product: {}
    }
  },
  mixins: [backTopMixin],
  created() {
    this.iid = this.$route.params.iid;
    // 请求详情数据
    getDetail(this.iid).then(res => {
      // 获取顶部图片
      this.topImages = res.result.itemInfo.topImages;
      // 获取商品信息
      const data = res.result;
      this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services);
      // 获取商家信息
      this.shops = new Shops(data.shopInfo);
      // 获取商品详情
      this.detailInfo = data.detailInfo;
      // 获取参数信息
      this.paramInfo = new GoodsParam(data.itemParams.info, data.itemParams.rule);
      // 获取评论信息
      if (data.rate.cRate !== 0) {
        // 在详情页只显示一条评论
        this.commentInfo = data.rate.list[0];
      }
    });

    // 请求推荐数据
    getRecommend().then(res => {
      this.recommends = res.data.list;
    });
  },
  updated() {
    // 获取offsetTop值
    this.getOffsetTops();
  },
  methods: {
    ...mapActions(['addCart']),
    navBarClick(index) {
      // 导航栏点击移动对应位置
      this.$refs.scroll.scrollTo(0, -this.themeTopYs[index]);
    },
    getOffsetTops() {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(this.$refs.paramInfo.$el.offsetTop);
      this.themeTopYs.push(this.$refs.commentInfo.$el.offsetTop);
      this.themeTopYs.push(this.$refs.recommendsInfo.$el.offsetTop);
    },
    scroll(position) {
      const topYs = this.themeTopYs;
      if (-position.y > topYs[0] && -position.y < topYs[1]) {
        this.$refs.navbar.currentIndex = 0;
      } else if(-position.y >= topYs[1] && -position.y < topYs[2]) {
        this.$refs.navbar.currentIndex = 1;
      } else if (-position.y >= topYs[2] && -position.y < topYs[3]) {
        this.$refs.navbar.currentIndex = 2;
      } else if (-position.y >= topYs[3]) {
        this.$refs.navbar.currentIndex = 3;
      }
      // 显示BackTop按钮
      this.isShowBackTop = -position.y > 1000;
    },
    addToCart() {
      // 加入购物车信息
      this.product.topImages = this.topImages[0];
      this.product.title =  this.goods.title;
      this.product.desc =  this.goods.desc;
      this.product.newPrice =  this.goods.calPrice;
      this.product.isActive = true;
      this.product.iid =  this.iid;

      this.addCart(this.product).then(res => {
        this.$toast.show(res, 1500);
      });
    }
  },
  mounted() {
    const refresh = debounce(this.$refs.scroll.refresh, 200);
    this.$bus.$on('imgLoad', () => {
      refresh();
    });
  }
}
</script>

<style scoped>
  .detail {
    position: relative;
    height: 100vh;
    background-color: #fff;
    z-index: 9;
  }

  .scroll-content {
    position: relative;
    top: 0;
    overflow: hidden;
    height: calc(100% - 4.65rem);
  }
</style>
