<template>
  <div class="base-info" v-if="Object.keys(goods).length !== 0">
    <h3 class="title">{{ goods.title }}</h3>
    <div class="price-wrap">
      <span class="price">{{ goods.price }}</span>
      <span class="old-price">{{ goods.oldPrice }}</span>
      <span class="discount">{{ goods.discount }}</span>
    </div>
    <div class="info-other">
      <span>{{ goods.columns[0] }}</span>
      <span>{{ goods.columns[1] }}</span>
      <span>{{ goods.services[0].name }}</span>
    </div>
    <div class="benefits">
      <div class="benefits-item" v-for="(item, index) in goods.services" 
        v-if="index !== 0 && item.name != '延误必赔'" :class="{'benefits-item-two': getServeLength}">
        <img :src="item.icon" alt="">
        <span>{{ item.name }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    goods: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data () {
    return {
    }
  },
  computed: {
    getServeLength() {
      // 显示2栏服务时，应用flex: 1；样式
      return this.goods.services.length == 3;
    }
  }
}
</script>

<style scoped>
  .title {
    text-indent: 0.5em;
    font-size: 0.9rem;
    margin: 0.5rem;
  }

  .price-wrap span {
    margin-left: 0.5rem;
  }

  .price {
    font-size: 1.25rem;
    color: var(--color-tint);
  }

  .old-price {
    font-size: 0.7rem;
    color: #9e9e9e;
    text-decoration: line-through;
  }

  .discount {
    position: relative;
    top: -0.4rem;
    font-size: 0.7rem;
    color: #fff;
    padding: 0.1rem 0.5rem;
    border-radius: 10px;
    background-color: var(--color-tint);
  }

  .info-other {
    display: flex;
    color: #b6b6b6;
    font-size: 0.65rem;
    padding: 1.5rem 0 0.5rem;
    margin: 0 0.5rem 1rem;
    border-bottom: 1px solid #d1d1d1;
    justify-content: space-between;
  }

  .benefits {
    display: flex;
    justify-content: space-between;
    padding: 1.2rem 0.5rem;
    font-size: 0.65rem;
    border-bottom: 0.3rem solid #ececec;
  }

  .benefits-item-two {
    flex: 1;
    text-align: center;
  }

  .benefits img {
    width: 0.7rem;
    margin-top: -0.15rem;
    vertical-align: middle;
  }

</style>
