<template>
  <div class="detail-shop-info" v-if="getShops">
    <div class="logo">
      <img :src="shops.shopLogo" alt="">
      <h3>{{ shops.name }}</h3>
    </div>
    <div class="shop-info clear-fix">
      <div class="achievement">
        <div>
          <h3>{{ getCSells }}</h3>
          <span>总销量</span>
        </div>
        <div>
          <h3>{{ shops.cGoods }}</h3>
          <span>全部宝贝</span>
        </div>
      </div>
      <div class="score">
        <ul>
          <li v-for="item in shops.score">{{ item.name }}</li>
        </ul>
        <ul>
          <li v-for="item in shops.score" :class="{high: item.isBetter, low: !item.isBetter}">{{ item.score }}</li>
        </ul>
        <ul>
          <li v-for="item in shops.score">
            <span :class="{highb: item.isBetter, lowb: !item.isBetter}">{{ isBetter(item) }}</span>
          </li>
        </ul>
      </div>
    </div>
    <div class="enter-shop">
      <a :href="shops.shopUrl">进店逛逛</a>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    shops: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data () {
    return {
    }
  },
  methods: {
    isBetter(item) {
      return item.isBetter ? '高' : '低';
    }
  },
  computed: {
    getShops() {
      return Object.keys(this.shops).length !== 0;
    },
    getCSells() {
      return (this.shops.cSells >= 10000) ? (this.shops.cSells / 10000).toFixed(1) + '万' : this.shops.cSells;
    },
  }
}
</script>

<style scoped>
  .detail-shop-info {
    padding-bottom: 1.5rem;
    border-bottom: 0.5rem solid #eee;
  }

  .logo {
    padding: 1.5rem 0.5rem;
  }

  .logo img {
    vertical-align: middle;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: 0.05rem solid #cfcfcf;
  }

  .logo h3 {
    display: inline-block;
    font-size: 1rem;
    font-weight: 400;
    margin-left: 0.5rem;
    height: 2rem;
    line-height: 2rem;
  }
  
  .shop-info {
    font-size: 0.65rem;
    padding-bottom: 1.2rem;
  }

  .achievement,
  .achievement div,
  .score {    
    float: left;
    width: 50%;
  }

  .achievement {
    margin-top: 1rem;
    text-align: center;
    border-right: 1px solid #ececec;
  }

  .achievement h3 {
    font-size: 0.9rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
  }

  .score {
    display: flex;
    justify-content: space-around;
    padding-left: 1rem;    
  }

  .score li {
    height: 1.3rem;
    line-height: 1.3rem;
  }

  .score li span {
    color: #fff;
  }

  .high {
    color: #e67756;
  }
  
  .highb {
    background-color: #e67756;
  }

  .low {
    color: #00ad2b;
  }

  .lowb {
    background-color: #00ad2b;
  }

  .enter-shops {
    display: block;
    margin: 1rem auto;
    border: 0;
    border-radius: 5px;
    padding: 0.5rem 3rem;
    font-size: 18px;
    outline: none;
    color: var(--color-text);
    background-color: #eee;
  }

  .enter-shop a {
    display: block;
    width: 10rem;
    padding: 0.5rem 0;
    font-size: 0.9rem;
    text-align: center;
    margin: 0 auto;
    border-radius: 5px;
    background-color: #eee;
  }
</style>
