import detail from "../../public/data/detail/detail.json";
import recommend from "../../public/data/detail/recommend.json";

export function getDetail(id) {
  let arr = Object.keys(detail);
  let r = false; 
  for (let i = 0; i < arr.length; i++) {
    if (id == arr[i]) {
      r = true;
      break;
    }
  }
  if (!r) {
    // 如果没有选中的数据，随机显示
    let key = arr[Math.round(Math.random()*(arr.length-1))];
    return new Promise((resolve, rejects) => {
      resolve(detail[key]);
    })
  }
  return new Promise((resolve, reject) => {
    resolve(detail[id]);
  });
}

export function getRecommend() {
  return new Promise((resolve, reject) => {
    resolve(recommend);
  });
}

export class Goods {
  constructor (itemInfo, columns, services) {
    this.title = itemInfo.title;
    this.price = itemInfo.price;
    this.calPrice = itemInfo.lowNowPrice * 1;
    this.desc = itemInfo.desc;
    this.oldPrice = itemInfo.oldPrice;
    this.discount = itemInfo.discountDesc;
    this.columns = columns;
    this.services = services;
  }
}

export class Shops {
  constructor (shopInfo) {
    this.shopLogo = shopInfo.shopLogo;
    this.name = shopInfo.name;
    this.cFans = shopInfo.cFans;
    this.cSells = shopInfo.cSells;
    this.score = shopInfo.score;
    this.cGoods = shopInfo.cGoods;
    this.shopUrl = shopInfo.shopUrl;
  }
}

export class GoodsParam {
  constructor(info, rule) {
    // 注: images可能没有值(某些商品有值, 某些没有值)
    this.image = info.images ? info.images[0] : '';
    this.infos = info.set;
    this.sizes = rule.tables;
  }
}

