<template>
  <div class="goods-list-item" @click="itemClick">
    <img :src="showImage" alt="" @load="imgLoadEnd">
    <div class="goods-info">
      <p>{{ goodsItem.title }}</p>
      <div class="goods-data">
        <span class="price">{{ goodsItem.price }}</span>
        <span class="collect">{{ goodsItem.cfav }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  props: {
    goodsItem: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    imgLoadEnd() {
      this.$bus.$emit('imgLoadEnd');
    },
    itemClick() {
      this.$router.push('/detail/' + this.goodsItem.iid);
    }
  },
  computed: {
    showImage() {
      return this.goodsItem.image || this.goodsItem.show.img;
    }
  },
}
</script>

<style scoped>
  .goods-list-item {
    position: relative;
    width: 48%;
    padding-bottom: 40px;
  }

  .goods-list-item img {
    display: block;
    width: 100%;
    border-radius: 5px;
  }

  .goods-info {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 2px;
    font-size: 12px;
  }

  .goods-info p {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin: 5px 0;
  }

  .goods-data {
    text-align: center;
  }

  .goods-info .price {
    color: var(--color-tint);
    margin-right: 20px;
  }

  .goods-info .collect {
    position: relative;
  }

  .goods-info .collect::before {
    content: '';
    position: absolute;
    left: -15px;
    top: -1px;
    width: 14px;
    height: 14px;
    background: url("~assets/img/common/collect.svg") 0 0/14px 14px;
  }

</style>
